html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.875;
}

.noscroll {
  overflow: hidden;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (max-width: 479px) {
  .container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .container {
    width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .container {
    width: 768px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

picture {
  margin: 0;
  padding: 0;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-family: inherit;
}

:root {
  --focus-pinc-color: #d41443;
  --accent-salmon-color: #ffa5ba;
  --text-black-color: black;
  --text-gray-color: #907e82;
  --focus-white-color: #fff;
  --main-transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --bcg-pink-color: #ffb8ca;
  --pink-btn-hover: #c7133e;
  --white-btn-hover: #ecebeb;
}

.header {
  width: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
}

.header-container {
  align-items: center;
  display: flex;
}

@media screen and (min-width: 320px) {
  .header-container {
    justify-content: space-between;
    padding-top: 8px;
  }
}

@media screen and (min-width: 768px) {
  .header-container {
    padding-top: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .header-container {
    justify-content: space-between;
    padding-top: 37px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .logo {
    margin-right: auto;
  }
}

@media screen and (min-width: 1280px) {
  .header-nav {
    align-items: center;
    display: flex;
  }
}

.header-nav__link {
  color: var(--focus-white-color);
  transition: color var(--main-transition);
  font-weight: 700;
  line-height: 1.31;
  text-decoration: none;
}

.header-nav__link:hover, .header-nav__link:focus {
  color: var(--focus-pinc-color);
}

@media screen and (max-width: 1279px) {
  .header-nav__link {
    height: 100%;
    width: 100%;
    align-items: center;
    padding-left: 20px;
    display: flex;
  }
}

.header-nav__item:not(:last-child) {
  margin-right: 35px;
}

@media screen and (max-width: 1279px) {
  .header-nav__item {
    width: 100%;
    height: 42px;
    border-top: 1px solid #e18298;
    align-items: center;
    display: flex;
  }
}

@media screen and (max-width: 1279px) {
  .header-nav__item:last-child {
    border-bottom: 1px solid #e18298;
  }
}

.header-button {
  transition: background-color var(--main-transition);
}

@media screen and (max-width: 767px) {
  .header-button {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .header-button {
    width: 160px;
    height: 44px;
    background-color: var(--focus-white-color);
    color: var(--focus-pinc-color);
    border-radius: 22px;
    justify-content: center;
    align-items: center;
    padding: 0;
    font-weight: 700;
    line-height: 1.31;
    display: flex;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .header-button {
    margin-left: 32px;
  }
}

.header-button:hover, .header-button:focus {
  background-color: var(--white-btn-hover);
}

.header-button__icon {
  fill: var(--focus-pinc-color);
  margin-left: 12px;
}

.menu-open-btn {
  width: 20px;
  height: 40px;
  justify-content: center;
  align-items: center;
  padding-left: 0;
  padding-right: 0;
  display: flex;
}

.menu-open-btn:hover .menu-open-icon, .menu-open-btn:focus .menu-open-icon {
  stroke: var(--focus-white-color);
}

@media screen and (min-width: 1280px) {
  .menu-open-btn {
    display: none;
  }
}

.menu-open-icon {
  stroke: var(--focus-pinc-color);
  stroke-linejoin: miter;
  stroke-linecap: butt;
  stroke-miterlimit: 4;
  stroke-width: 2.285px;
  transition: stroke var(--main-transition);
}

.header-button-mobile {
  width: 210px;
  height: 44px;
  background-color: var(--focus-white-color);
  color: var(--focus-pinc-color);
  transition: background-color var(--main-transition);
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

@media screen and (max-width: 319px) {
  .header-button-mobile {
    width: 140px;
  }
}

@media screen and (min-width: 1280px) {
  .header-button-mobile {
    display: none;
  }
}

.header-button-mobile:hover, .header-button-mobile:focus {
  background-color: var(--white-btn-hover);
}

.header-button-mobile .header-button__icon {
  margin-left: 16px;
}

@media screen and (max-width: 1279px) {
  .site-nav-wrap {
    width: 50vw;
    height: 100vh;
    opacity: 0;
    transition: transform var(--main-transition), opacity var(--main-transition);
    background-color: #e17992;
    padding: 60px 0;
    position: fixed;
    top: 0;
    right: 0;
    transform: translate(100%);
  }
}

@media screen and (max-width: 480px) {
  .site-nav-wrap {
    width: 75vw;
    max-width: 254px;
  }
}

@media screen and (max-width: 1279px) {
  .is-open {
    opacity: 1;
    transform: translate(0);
  }
}

.menu-open-btn.is-opem {
  display: none;
}

.menu-close {
  width: 20px;
  height: 20px;
  background: inherit;
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
}

.menu-close:after, .menu-close:before {
  content: "";
  width: 100%;
  height: 1px;
  transition: background-color var(--main-transition);
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 0;
}

.menu-close:before {
  transform: rotate(45deg);
}

.menu-close:after {
  transform: rotate(-45deg);
}

.menu-close:hover:after, .menu-close:hover:before, .menu-close:focus:after, .menu-close:focus:before {
  background-color: var(--pink-btn-hover);
}

@media screen and (min-width: 1280px) {
  .menu-close {
    display: none;
  }
}

.header-backdrop {
  width: 100%;
  height: 100%;
  z-index: 300;
  opacity: 1;
  transition: opacity var(--main-transition), visibility var(--main-transition);
  background-color: rgba(0, 0, 0, .3);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.header-modal {
  width: 93vw;
  height: 100%;
  max-width: 450px;
  max-height: 550px;
  background-color: var(--focus-white-color);
  transition: transform var(--main-transition);
  border-radius: 30px;
  padding: 35px 15px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.header-backdrop.is-hidden .header-modal {
  transform: translate(-50%, -50%)scale(1.3);
}

@media screen and (max-width: 319px) {
  .header-modal {
    max-height: 450px;
    padding: 40px 10px;
    overflow-y: visible;
  }
}

@media screen and (min-width: 768px) {
  .header-modal {
    max-height: 600px;
    padding: 50px 20px 30px;
    overflow-y: visible;
  }
}

@media screen and (min-width: 1280px) {
  .header-modal {
    max-height: 640px;
  }
}

.modal-header-close-btn {
  width: 15px;
  height: 15px;
  background: inherit;
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
}

.modal-header-close-btn:after, .modal-header-close-btn:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--accent-salmon-color);
  transition: background-color var(--main-transition);
  position: absolute;
  top: 50%;
  left: 0;
}

.modal-header-close-btn:before {
  transform: rotate(45deg);
}

.modal-header-close-btn:after {
  transform: rotate(-45deg);
}

.modal-header-close-btn:hover:after, .modal-header-close-btn:hover:before, .modal-header-close-btn:focus:after, .modal-header-close-btn:focus:before {
  background-color: var(--pink-btn-hover);
}

.modal-title {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--pink-btn-hover);
  text-align: center;
  margin-bottom: 12px;
  font-family: Titan One, sans-serif;
  font-size: 22px;
  line-height: 1.12;
}

@media screen and (max-width: 319px) {
  .modal-title {
    font-size: 16px;
  }
}

@media screen and (min-width: 768px) {
  .modal-title {
    font-size: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .modal-title {
    font-size: 38px;
  }
}

.form-field {
  margin-bottom: 15px;
}

@media screen and (max-width: 319px) {
  .form-field {
    margin-bottom: 7px;
  }
}

.form-label {
  color: var(--focus-pinc-color);
  letter-spacing: .04em;
  margin-bottom: 3px;
  padding-left: 10px;
  font-size: 12px;
  line-height: 1.3;
  display: block;
}

@media screen and (max-width: 319px) {
