@media screen and (min-width: 1280px) {
  .form-label {
    font-size: 16px;
  }
}

.form-input {
  width: 100%;
  height: 35px;
  border: 1px solid var(--accent-salmon-color);
  color: var(--pink-btn-hover);
  letter-spacing: .04em;
  transition: border var(--main-transition);
  background-color: rgba(255, 165, 186, .1);
  border-radius: 10px;
  padding: 8px 10px;
  font-size: 14px;
  line-height: 1.17;
}

.form-input:hover, .form-input:focus {
  cursor: pointer;
  border: 1px solid var(--pink-btn-hover);
  outline: none;
}

.form-input::-ms-input-placeholder {
  color: rgba(199, 19, 62, .5);
  font-size: 12px;
}

.form-input::placeholder {
  color: rgba(199, 19, 62, .5);
  font-size: 12px;
}

@media screen and (max-width: 319px) {
  .form-input::-ms-input-placeholder {
    font-size: 8px;
  }

  .form-input::placeholder {
