  height: 100px;
  border: 1px solid var(--accent-salmon-color);
  resize: none;
  color: var(--pink-btn-hover);
  letter-spacing: .04em;
  transition: border var(--main-transition);
  background-color: rgba(255, 165, 186, .1);
  border-radius: 10px;
  margin-top: 4px;
  margin-bottom: -4px;
  padding: 10px;
  font-size: 12px;
  line-height: 1.17;
}

.form-comment::-ms-input-placeholder {
  color: rgba(199, 19, 62, .5);
  font-size: 12px;
}

.form-comment::placeholder {
  color: rgba(199, 19, 62, .5);
  font-size: 12px;
}

@media screen and (max-width: 319px) {
  .form-comment::-ms-input-placeholder {
    font-size: 8px;
  }

  .form-comment::placeholder {
    font-size: 8px;
  }
}

.form-comment:hover, .form-comment:focus {
  cursor: pointer;
  border: .5px solid var(--pink-btn-hover);
  outline: none;
}

@media screen and (max-width: 319px) {
  .form-comment {
    height: 60px;
    padding-top: 5px;
