}

.form-btn {
  color: var(--focus-white-color);
  background-color: var(--focus-pinc-color);
  letter-spacing: .04em;
  text-align: center;
  transition: background-color var(--main-transition), box-shadow var(--main-transition);
  border-radius: 20px;
  margin: 0 auto;
  padding: 10px 40px;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.25;
  display: block;
}

@media screen and (max-width: 319px) {
