.checkbox-label {
  justify-content: center;
  align-items: center;
  font-size: 12px;
  line-height: 1.17;
  display: flex;
}

.checkbox-box {
  width: 16px;
  height: 14px;
  border: 1px solid var(--accent-salmon-color);
  transition: background-color var(--main-transition), border var(--main-transition), box-shadow var(--main-transition);
  border-radius: 4px;
  justify-content: center;
