.checkbox-icon {
  fill: rgba(0, 0, 0, 0);
  transition: fill var(--main-transition);
}

.checkbox-input:focus + .checkbox-label .checkbox-box, .checkbox-input:hover + .checkbox-label .checkbox-box {
  border: 1px solid var(--pink-btn-hover);
  cursor: pointer;
}

.checkbox-input:checked + .checkbox-label .checkbox-box {
  background-color: var(--focus-pinc-color);
  border: 1px solid var(--focus-pinc-color);
  box-shadow: none;
}

.checkbox-input:checked + .checkbox-label .checkbox-icon {
